
@mixin action-btn(){
  display: inline-block;
  float: left;
  padding-left: 5px;
}
.action-btn-wrapper {
    > a, form {
      @include action-btn()
    }
}

.dropzone {
  border: 5px dotted #e5e5e5 !important;
}
.dropzone.dz-clickable .dz-message, .dropzone.dz-clickable .dz-message * {
  font-size: 1.5em;
}

.quotation-thumbnail-wrapper {
  height: 200px;
  overflow: hidden;
  img {
    max-height: 160px;
  }
}

@import "comment";